import { useEffect } from "react";
import { connect } from 'react-redux';
import * as action from '../nginx/store/action'
// import cookie from 'react-cookies'

var Socket //
var reconnect = false //
var WS_IP //ip
var token = ""+Date.parse(new Date())
 const   WebSocketFc = (props) =>{
    useEffect(() =>{
        WS_IP = "ws://180.76.112.178:8080/wsresult?" + token;
    Ws_Connect();
    },[])

   const Ws_Connect = () =>{
    
        Socket = new WebSocket(WS_IP);
        initWs();
    }

   const initWs = () =>{
         //
        Socket.onopen = function(){
            //
            heartCheck.reset().start()
        }
        //
        Socket.onmessage = function(info){
            //
           const data = JSON.parse(info.data)
           console.log(data)
            switch(data.type){
                case 'heart' :
                    heartCheck.reset().start();
                    console.log("websocket", data);
                    break;
                case 'potos':
                    props.getAllPhoto()
                default: 
                    console.log("websocket>>>",data)
            }
        }

        //
        Socket.onerror = function(event){
            console.log("",event) 
            Socket.close()//socketonclose
           
        }
        //
        Socket.onclose = function(){
            // console.log("")
            heartCheck.reset(); //
            //
            reconnect = true;
            websocketReconnec();
        }

        //
        var heartCheck = {
            timeout: 6000,
            timeoutObj: null,
            serverTimeoutObj: null,
            reset: function(){
                if(this.timeoutObj){
                    //
                    clearTimeout(this.timeoutObj)
                    clearTimeout(this.serverTimeoutObj)
                }
                return this;
            },
            start: function(){
                //
                 //  
                 const data = {
                    type: "heart", data: { "message": "" },
                    from: 'liu'
              };
                this.timeoutObj = setTimeout(() => {
                    try{
                        console.log("")
                         Socket.send(JSON.stringify(data));
                    }catch (e) {
                        console.log("")
                        Socket = null;
                        websocketReconnec();
                    }
                }, this.timeout);
            }
        }

    }
   
    const websocketReconnec = () =>{
        if(!reconnect) return;
        //
        tt&&clearTimeout(tt)
        if(reconnect){
            var tt = setTimeout(() => {
            console.error("");
            Ws_Connect();
            reconnect = false;
            }, 3000);
        }
    }
    //
    return null;
}


const mapStateToPorps = state => {
    // console.log(state.excelDemo.data.excelExampleTemplates)
    // debugger
    console.log(state)
    // excel
    return ({ data: state.nginx.data })
};


const mapDispatchToProps = dispatch => ({
    // 
    getAllPhoto() {
        dispatch(action.getAllPhoto());
    }
});
export default connect(mapStateToPorps, mapDispatchToProps)(WebSocketFc)
